import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-list-and-faces',
  templateUrl: './list-and-faces.component.html',
  styleUrls: ['./list-and-faces.component.scss']
})
export class ListAndFacesComponent implements OnInit {

  lists: any = [
    {title: 'First list', comment: 'w3dsg4523df'},
    {title: 'Second list', comment: 'w3dsg4523df'},
    {title: 'Third list', comment: 'w3dsg4523df'},
    {title: 'Fourth list', comment: 'w3dsg4523df'},
    {title: 'Fifth list', comment: 'w3dsg4523df'}
  ];
  current_list: any = 0;

  elements: any = [];
  headElements = ['id', 'first', 'last', 'handle'];

  constructor() { }

  ngOnInit() {
    for (let i = 1; i <= 11; i++) {
      this.elements.push({ id: i, first: 'User ' + i, last: 'Name ' + i, handle: 'Handle ' + i });
    }
  }

  pageChange(pageNum) {
    console.log('pageNum: ', pageNum);
  }

}
