import { NgModule } from '@angular/core';
import { MDBBootstrapModule } from 'angular-bootstrap-md';
import { CommonModule } from '@angular/common';

import { HttpClientModule } from '@angular/common/http';
import { StoreModule } from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';
import { StoreRouterConnectingModule } from '@ngrx/router-store';

import { adminModuleReducers } from './store/reducers/admin-module.reducers';
import { environment } from '../../environments/environment';
import { EventEffects } from './store/effects/event.effects';

import { EventService } from './services/event.service';

import { AdminRoutes } from '@admin/admin.routes';

import { NgxPaginationModule } from 'ngx-pagination';

import { EventsComponent } from '@admin/components/events/events.component';
import { ListAndFacesComponent } from '@admin/components/list-and-faces/list-and-faces.component';
import { HandlersComponent } from '@admin/components/handlers/handlers.component';
import { DashboardComponent } from '@admin/components/dashboard/dashboard.component';
import { AdminWrapperComponent } from '@admin/components/admin-wrapper/admin-wrapper.component';
import { SwitchTabComponent } from './components/switch-tab/switch-tab.component';
import { PaginationBarComponent } from './components/pagination-bar/pagination-bar.component';

@NgModule({
  declarations: [
    EventsComponent,
    ListAndFacesComponent,
    HandlersComponent,
    DashboardComponent,
    AdminWrapperComponent,
    SwitchTabComponent,
    PaginationBarComponent
  ],
  imports: [
    HttpClientModule,
    StoreModule.forRoot(adminModuleReducers),
    EffectsModule.forRoot([EventEffects]),
    StoreRouterConnectingModule.forRoot({stateKey: 'router'}),
    MDBBootstrapModule.forRoot(),
    CommonModule,
    AdminRoutes,
    NgxPaginationModule
  ],
  providers: [EventService]
})
export class AdminModule { }
