import {
  EventActions,
  EEventActions
} from '../actions/event.action';
import {
  initialEventState,
  EventState
} from '../states/event.state';

export function eventReducers (
  state = initialEventState,
  action: EventActions
): EventState {
  switch (action.type) {
    case EEventActions.GET_EVENTS: {
      return {
        ...state,
        invokeStatus: true,
        loader: true,
      };
    }
    case EEventActions.GET_EVENTS_SUCCESS: {
      return {
        ...state,
        events: action.payload,
        invokeStatus: true,
        loader: false
      };
    }
    case EEventActions.GET_EVENTS_ERROR: {
      console.log('action.payload err: ', action.payload);
      return {
        ...state,
        message: action.payload,
        invokeStatus: false,
        loader: false
      };
    }
    case EEventActions.GET_EVENT_SUCCESS: {
      return {
        ...state,
        selectedEvent: action.payload
      };
    }
    default:
      return state;
  }
}