import { Component, OnInit } from '@angular/core';
import { Store, select } from '@ngrx/store';

import { GetEvents } from '../../store/actions/event.action';
import { AdminModuleState } from '../../store/states/admin-module.state'
import {
  selectEventList,
  selectEventLoader,
  selectEventInvokeStatus,
  selectEventMessage
} from '../../store/selectors/event.selectors';

@Component({
  selector: 'app-events',
  templateUrl: './events.component.html',
  styleUrls: ['./events.component.scss']
})
export class EventsComponent implements OnInit {

  events$ = this._store.pipe(select(selectEventList));
  eventsLoader$ = this._store.pipe(select(selectEventLoader));
  eventsInvokeStatus$ = this._store.pipe(select(selectEventInvokeStatus));
  eventsMessage$ = this._store.pipe(select(selectEventMessage));

  headElements = [
    {label: 'TIME', id: 'create_time'},
    {label: 'SOURCE', id: 'source'},
    {label: 'AGE/GENDER', id: 'age'},
    {label: 'TOP LIST', id: 'top_list'},
    {label: 'TOP ID', id: 'top_id'},
    {label: 'TOP %', id: 'top_percent'}
  ];

  constructor(
    private _store: Store<AdminModuleState>
  ) { }

  ngOnInit() {
    this._store.dispatch(new GetEvents());
  }

  pageChange(pageNum) {
    console.log('pageNum: ', pageNum);
  }

}
