import { Action } from '@ngrx/store'; 

import { Event } from '../../models/event.model'; 

export enum EEventActions {
  GET_EVENTS = '[Event] Get Events',
  GET_EVENTS_SUCCESS = '[Event] Get Events Success',
  GET_EVENTS_ERROR = '[Event] Get Events Error',
  GET_EVENT = '[Event] Get Event',
  GET_EVENT_SUCCESS = '[Event] Get Event Success'
}

export class GetEvents implements Action {
  public readonly type = EEventActions.GET_EVENTS; 
} 

export class GetEventsSuccess implements Action {
  public readonly type = EEventActions.GET_EVENTS_SUCCESS; 
  constructor(public payload: Event[]) {}
}

export class GetEventsError implements Action {
  public readonly type = EEventActions.GET_EVENTS_ERROR; 
  constructor(public payload: string) {}
}

export class GetEvent implements Action {
  public readonly type = EEventActions.GET_EVENT; 
  constructor(public payload: number) {}
} 

export class GetEventSuccess implements Action {
  readonly type = EEventActions.GET_EVENT_SUCCESS;
  constructor(public payload: Event) {}
}



export type EventActions =
  | GetEvents
  | GetEventsSuccess
  | GetEventsError
  | GetEvent
  | GetEventSuccess;