import { Injectable } from '@angular/core';
import { Effect, ofType, Actions } from '@ngrx/effects';
import { Store, select } from '@ngrx/store';
import { of } from 'rxjs';
import { switchMap, map, withLatestFrom, catchError } from 'rxjs/operators';

import { AdminModuleState } from '../states/admin-module.state';

import {
  EEventActions,
  GetEventSuccess,
  GetEvent,
  GetEventsSuccess,
  GetEventsError,
  GetEvents
} from '../actions/event.action';
import { EventService } from '../../services/event.service';
import { selectEventList } from '../selectors/event.selectors';

@Injectable()
export class EventEffects {
  @Effect()
  getEvent$ = this._actions$.pipe(
    ofType<GetEvent>(EEventActions.GET_EVENT),
    map(action => action.payload),
    withLatestFrom(this._store.pipe(select(selectEventList))),
    switchMap(([id, events]) => {
      const selectedEvent = events.filter(event => 0 === +id)[0];
      return of(new GetEventSuccess(selectedEvent));
    })
  );

  @Effect()
  getEvents$ = this._actions$.pipe(
    ofType<GetEvents>(EEventActions.GET_EVENTS),
    switchMap(() => 
      this._eventService.getEvents().pipe(
        map((res: any) => new GetEventsSuccess(res)),
        catchError((err: any) => of(new GetEventsError(err.message)))
      ))
  );

  constructor(
    private _eventService: EventService,
    private _actions$: Actions,
    private _store: Store<AdminModuleState>
  ) {}
}
