import { createSelector } from '@ngrx/store';

import { AdminModuleState } from '../states/admin-module.state';
import { EventState } from '../states//event.state';

const selectEvents = (state: AdminModuleState) => state.events;

export const selectEventList = createSelector(
  selectEvents,
  (state: EventState) => state.events
);

export const selectSelectedEvent = createSelector(
  selectEvents,
  (state: EventState) => state.selectedEvent
);

export const selectEventLoader = createSelector(
  selectEvents,
  (state: EventState) => state.loader
);

export const selectEventInvokeStatus = createSelector(
  selectEvents,
  (state: EventState) => state.invokeStatus
);

export const selectEventMessage = createSelector(
  selectEvents,
  (state: EventState) => state.message
);